.border-container {
    background-color: #00467f;
    height: 10%;
    max-height: 100px;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    z-index: 1;
}

.top {
    position: absolute;
    left: 0;
    top: 0;
}

.bottom {
    position: absolute;
    left: 0;
    bottom: 0;
}

.header-bg {
    background-image: url(./img/jpeg/header.jpeg);
    width: 100vw;
    height: 100vh;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    position: absolute;
    bottom: 0;
    left: 0;
    z-index: 0;
}

.logo {
    position: absolute;
    z-index: 2;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    height: 40%;
    max-width: 300px;
    max-height: 300px;
}

.container {
    display: grid;
    grid-template-columns: 50% 50%;
    grid-template-rows: repeat(2, auto);
    justify-items: center;
    align-items: center;
    justify-content: space-between;
    position: absolute;
    top: 55%;
    left: 50%;
    transform: translate(-50%, -30%);
}

.notice {
    grid-column-start: span 2;
    background-color: #00467f;
    border-radius: 25px;
    border: 3px solid white;
    width: 90%;
    min-height: 100px;
    height: auto;
    padding: 0 5%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.notice p {
    text-align: center;
    font-family: 'Poppins', sans-serif;
    font-size: 24px;
    color: white;
}

.notice a {
    line-height: 4rem;
}

.notice a:link,
.notice a:visited {
    color: #ffffff;
}

.notice a:hover,
.notice a:active {
    color: #f8db0c;
}

.delivery-badge {
    grid-column-start: span 1;
    margin: 3%;
}

.delivery-badge a img {
    max-width: 90%;
    max-height: 8vh;
}

@media only screen and (max-width: 770px) {
    .notice p {
        font-size: 18px;
    }

    .container {
        grid-template-columns: 50% 50%;
    }

    .delivery-badge a img {
        max-width: 100%;
        max-height: 80px;
    }
}

@media only screen and (max-width: 1024px) {
    .logo {
        max-width: 30%;
        max-height: 30%;
    }

    .container {
        top: 45%;
    }
}

@media only screen and (max-width: 450px) {
    .notice {
        width: 85vw;
    }

    .notice a {
        font-size: 24px;
    }

    .logo {
        max-width: 180px;
        max-height: 180px;
    }

    .container {
        top: 45%;
    }
}

@media only screen and (max-width: 400px) {
    .notice p {
        font-size: 14px;
    }
}

@media only screen and (max-width: 350px) {
    .notice p {
        font-size: 16px;
    }

    .logo {
        max-width: 140px;
        max-height: 140px;
    }
}

@media only screen and (max-height: 500px) {
    .notice {
        min-height: 50px;
    }

    .notice p {
        font-size: 12px;
    }

    .delivery-badge {
        margin: 2%;
    }
}
